<template>
  <div class="text-main" >
    <div class="contain">
      <div class="mybutton">
        <div class="topLeft">
          <p id="showTime">倒计时  {{timeMes}}</p>
        </div>
        <div class="topRight" @click="getOver()"><a class="end">交卷</a></div>
      </div>
      <!--题目内容-->
      <div class="swiper-container  body">
        <div class="swiper-wrapper" >
          <div  v-for="(item, index) in receiveData" :key="index" class="swiper-slide">
            <div class="fontstyle">
              <div v-if="item.exe.typeCode == '0101'" class="typePhoto">单选题</div>
              <div v-if="item.exe.typeCode == '0102'" class="typePhoto">多选题</div>
              <div v-if="item.exe.typeCode == '0103'" class="typePhoto">判断题</div>
              <div v-if="item.exe.typeCode == '0104'" class="typePhoto">不定项</div>
              <div class="text_mian_title"> &nbsp;&nbsp;{{pageIndex}}.{{item.exe.stem}}</div>
            </div>
            <div v-for="(i,index) in item.exe.exeOptionList" :key="index" class="swiper-slide options" >
              <div class="text-main-options" >
                <div style="width: 0.42rem;height: 0.42rem;background-size:100% 100%;" :class="String.fromCharCode(65+index)" :id="item.exerciseId+'_'+String.fromCharCode(65 + index)"   @click="select(item,i)"></div>&nbsp;&nbsp;&nbsp;&nbsp;
                <div style="width: 88%;font-size:0.36rem;line-height: 0.48rem;">
                  <span :id=" String.fromCharCode(65 + index) + '_' + item.exerciseId " @click="select(item,i)">{{i.options}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 倒计时提醒 -->
      <div class="lackofTime">
        <img class="timeWarning" src="../../assets/image/考试不足弹窗.png" alt="">
        <img class="residueTime" src="../../assets/image/正规考试.png" alt="">
        <div class="timeWarning" style="display:none;">
          <span class="tenSeconds"></span>
          <br>
          <span style="position:absolute;top: 15%;top: 86%;width: 2rem;display: flex;    left: 10%;">后自动交卷</span>
        </div>
        <div class="residueTime">
          <span style="color:#fff;position:absolute;left:35%;top: 20%;">考试</span>
          <br>
          <span style="color:#fff;position:absolute;left:18%;">仅剩5分钟</span>
          <span class="fiveSeconds"></span>
        </div>
      </div>

      <!-- 交卷提醒 -->
      <div class="textMainBox">
        <div class="smallBox">
          <img src="../../assets/image/新交卷弹窗.png" alt="">
          <div class='word'>
            <div style="display: flex;justify-content: center;align-items: center;flex-direction: column">
              <div class="time">
                未答<span style="font-size:0.46rem;color:#FB6C6C;padding:0 0.1rem;">{{receiveData.length - selectList.length}}</span>题
              </div>
              <div class="do">
                  <span>剩余时间</span>
                  <span class="timer">{{timeMes}}</span>
              </div>
            </div>
            <div class="startText-mess-two">
                <div class="startText-mess-two-wait" @click="nowPaper">现在交卷</div>
                <div class="startText-mess-two-wait2">现在交卷</div>
                <div style="width:0.021rem;height:0.42rem;background:rgba(231,236,244,1);margin-top:0.3rem;"></div>
                <div class="startText-mess-two-go" @click="continuePaper" >继续答题</div>
            </div>
          </div>
        </div>
      </div>
      <!-- 交卷成功 -->
      <div class="successBox">
        <div class="success">
          <img src="../../assets/image/交卷成功背景.png" alt="">
          <div style="font-size:0.34rem;color:rgba(235,246,244,1);margin-top:0.4rem;">交卷成功</div>
        </div>
      </div>
      <!-- 答题卡 -->
      <div class="textMainBox-dtk" >
        <div class='dkt-head' @click="closeDtkBox()"></div>
        <div class="dkt-message">
          <div class="dkt-message-title"></div>
          <div class="dkt-message-content"></div>
        </div> 
      </div>
      
    </div>
    <!--底下的导航栏-->
    <div class="bottom-mes">
      <div class="bottom-mes-mes-dtk">
        <div class="bottom-mes-mes-dtk-1">
          <div class="bottom-mes-mes-1 " @click="bjAdd()"></div>
          <div class="bottom-mes-mes-1-1">{{bjList.length}}</div>
        </div>
        <div class="bottom-mes-mes-dtk-2">
          <div class="bottom-mes-mes-2"></div>
          <div class="bottom-mes-mes-2-2">{{selectList.length}}</div>
        </div>
        <div class="bottom-mes-mes-dtk-3">
          <div class="bottom-mes-mes-3"></div>
          <div class="bottom-mes-mes-3-3">{{receiveData.length - selectList.length}}</div>
        </div>
        <div class="bottom-mes-mes-dtk-4" @click="showDTK()">
          <div class="bottom-mes-mes-5">
            <span style="color: #333;">{{pageIndex}}</span>
            <span style='color:#999999'>/</span>
            <span style="color: #666;">{{receiveData.length}}</span>
          </div>
        </div>
      </div>
    </div>
    <!-- 切屏提示 -->
    <div class='bigBox1'>
      <div class='smallBox1'>
        <img src="../../assets/image/开始考试的背景.png" alt="">
        <div class='boxWord1'>您已切出考试界面1次<br/>超过2次系统将会自动交卷</div>
        <!-- <div class='boxWord1' style="margin-top:0.5rem">超过2次系统将会自动交卷</div> -->
        <div class="startText-mess-two1" @click.stop="comfirm1()">
          确定
        </div>
      </div>
    </div>
    <div class='bigBox2'>
      <div class='smallBox2'>
        <img src="../../assets/image/开始考试的背景.png" alt="">
        <div class='boxWord2'>您已切出考试界面2次<br/>超过2次系统将会自动交卷</div>
        <!-- <div class='boxWord1' style="margin-top:0.5rem">超过2次系统将会自动交卷</div> -->
        <div class="startText-mess-two2" @click.stop="comfirm2()">
          确定
        </div>
      </div>
    </div>
    <div class='bigBox3'>
      <div class='smallBox3'>
        <img src="../../assets/image/开始考试的背景.png" alt="">
        <div class='boxWord3'>您切出考试界面已超过2次<br/>系统已自动交卷</div>
        <div class="startText-mess-two3" @click.stop="comfirm3()">
          确定
        </div>
      </div>
    </div>
    <!-- 手势提醒界面 -->
    <div class='remindtextMainBox'>
      <div class='remindsmallBox'>
        <!-- <div style='margin-top:5rem;font-size:0.3rem;color:#fff;'>考试过程中为防止作弊，系统会随机拍照</div> -->
        <div class='remindImd'>
          <img src="../../assets/image/左右滑动@2x.png" alt="">
          <div style='position:absolute;left:0;bottom:0.8rem;font-size:0.24rem;color:#fff;width:1rem;text-align:center'>上一题</div>
          <div style='position:absolute;right:0;bottom:0.8rem;font-size:0.24rem;color:#fff;width:1rem;text-align:center'>下一题</div>
        </div>
        <div class='remindInformation'>左右滑动可切换上下题</div>
        <div class='remindButton' @click='IKnow()'>我知道了</div>
      </div>
    </div>
    <!-- 成绩计算界面 -->
   <uploadData :dialogVisible="uploadDialogVisible"></uploadData>
    <!-- 加载页面 -->
    <Loading :dialogVisible="verDialogVisible"></Loading>
    <!-- 确认弹出框 - 提示错误信息 -->
    <IsBox :dialogVisible="IsBoxDialogVisible" :mess="mess"  @inNesclose="IsBoxcloseVerDialogMethod"></IsBox>
    <!-- 无网络 -->
    <network :dialogVisible="networkDialogVisible" :mess="networkmess"></network>
  </div>

</template>

<script>
  import API from '@/api/api';
  import $ from 'jquery';
  import 'swiper/dist/css/swiper.css'
  import Loading from './../components/loading'
  import IsBox from './../components/isBox'
  import uploadData from './../components/uploadData'
  import network from './../components/network'

  import { Swiper, swiperSlide } from 'vue-awesome-swiper'
  export default {
    name: 'my',
    components: {
      Swiper,
      swiperSlide,
      Loading,
      IsBox,
      uploadData,
      network
    },
    data() {
      return {
        networkDialogVisible:false,   //无网络弹窗
        networkmess:'无网络连接，请查看网络',  //无网络提示语
        number:0, //退出到后台次数
        JTclick:0,
        id:'',//试题id
        duration: '',//考试时长
        selfExamId: '',//个人考试id
        pageIndex:1,// 当前页面的下标
        endTime: '',// 考试结束点
        isOver:false,// 是否停止考试（点击交卷时让计时器停止）,
        timeMes:'',
        isOpen:0, // 是否打开了交卷
        isHandel: 0, // 是否进行了5分钟提醒时间初始化
        isTenHandel: 0,//是否进行了10秒自动交卷时间初始化
        time:5, // 五分钟弹窗里面的5秒倒计时
        isOpenFive:false, // 是否显示提示窗
        fiveTime:'',
        isError : true , // 是否错误
        tenTime:'',//十秒倒计时
        bjList:[], // 表急了得题号的list
        selectList:[], // 已选择了答案的题号
        noSelectList:[], // 为选择了答案的题号
        swipperMes: '', //swipper的对象
        noSelectNum: 0, // 未做题的数量
        allDataList:{}, // 返回给接口的集合
        receiveData:[], // 试题选项的集合
        startTime:'',// 开始时间
        endtime:'', // 结束时间
        spendTime:'', // 耗时
        isShowFive: false,
        isShowFiveFale: false,
        isShowTen: false,
        isShowTenFale: false,
        isoverText: false,
        verDialogVisible:true,
        IsBoxDialogVisible : false , //是否打开提示信息
        uploadDialogVisible:false,
        mess : '' , // 提示信息
        isCanOver : false , // 是否已到时
        remindDialogVisible:false, //是否展示手势提醒弹窗
        isShowRemind:true,  //进入是否展示手势弹窗
        // isShowJJbox:false,  //是否展示交卷弹窗
        // isShowdtk:false  //是否展示答题卡
        backIndex:true,
      };
    },
    methods:{
      //调用接口获取数据
      getDataList(){
        const that = this;
        API.post('selectExamExercise', {
          examId : that.id
        },'json',(data,err)=>{
          if(!data.success){
            this.IsBoxDialogVisible = true;
            this.mess = data.msg;
            return;
          }
          if(data.success){
            that.allDataList = data;
            // localStorage.setItem('textMainallDataList',JSON.stringify(this.allDataList));
            that.allDataList.data.selfExamId = this.selfExamId;
            that.receiveData = that.allDataList.data.testpaperExerciseList;//试题的集合
            // localStorage.setItem('textMainreceiveData',JSON.stringify(this.receiveData));
            that.fiveTime = new Date(new Date().getTime() + 1000*6);//五分钟倒计时提示
            that.$nextTick(function () {
                this.showSwiper(0);
                setTimeout(function(){that.verDialogVisible = false;},1000);
            })
          }
        })
      },
      //第一次切屏弹窗
      comfirm1(){
        $('.text-main .bigBox1').css('display','none');
      },
      //第二次切屏弹窗
      comfirm2(){
        $('.text-main .bigBox2').css('display','none');
      },
      //第三次切屏弹窗
      comfirm3(){
        this.uploadDialogVisible = true;
        $('.text-main .bigBox3').css('display','none');
        this.isoverText = true;
        this.getEndTime();
        // $('.textMainBox').css('display','none');
        $('.now').css('display','none')
        $('.no-now').css('display','inline-block')
        $('.timeWarning').css('display','none');
        $('.lackofTime').css('display','none');
        this.isShowTen = false;
        this.isShowTenFale = false;
        this.isOpenFive = false;
        this.isShowFive = false;
      },
      //点击手势界面我知道了
      IKnow(){
        // this.remindDialogVisible = false;
        $('.text-main .remindtextMainBox').css('display','none');
        this.isShowRemind = false;
        localStorage.setItem('isShowRemind',this.isShowRemind);
        this.startTime = new Date().getTime();
        var nowTime= new Date(new Date().getTime() + 1000*60* this.duration + 1000);
        this.endTime = nowTime;
        this.getCode(); 
      },
      addClass(id){
        $('#'+id).attr('style','border-radius:0.3rem;background:#fff;');
        if(id == 'text'){
          $('#text1').removeAttr('style');
        }else{
          $('#text').removeAttr('style');
        }
      },
      //选择答案
      select(item,i){
        if(this.isCanOver){
          this.getOver();
          return;
        }
        //如果是单选题，点击选项时，先将选项全部为false
        if(item.exe.typeCode == '0101' || item.exe.typeCode == '0103'){
          for(var k = 0; k < item.exe.exeOptionList.length; k++){
            //把其他三个选项至为false
            if(i.id != item.exe.exeOptionList[k].id){
              item.exe.exeOptionList[k].selected = false;
              $("#"+item.exerciseId+'_'+String.fromCharCode(65 + k)).removeClass(String.fromCharCode(65 + k)+'-get');
              $("#"+item.exerciseId+'_'+String.fromCharCode(65 + k)).addClass(String.fromCharCode(65 + k));
              $("#"+String.fromCharCode(65 + k)+'_'+item.exerciseId).removeAttr("style");
              continue;
            }
            //判断是否选择了，如果未选择，就选择
            if(i.selected == false){
              i.selected = true;
              $("#"+item.exerciseId+'_'+String.fromCharCode(65 + k)).removeClass(String.fromCharCode(65 + k));
              $("#"+item.exerciseId+'_'+String.fromCharCode(65 + k)).addClass(String.fromCharCode(65 + k)+'-get');
              $("#"+String.fromCharCode(65 + k)+'_'+item.exerciseId).css("color","#04CD9D");
              continue;
            }
            i.selected = false;
            $("#"+item.exerciseId+'_'+String.fromCharCode(65 + k)).removeClass(String.fromCharCode(65 + k)+'-get');
            $("#"+item.exerciseId+'_'+String.fromCharCode(65 + k)).addClass(String.fromCharCode(65 + k));
            $("#"+String.fromCharCode(65 + k)+'_'+item.exerciseId).removeAttr("style");
          }
        }else if(item.exe.typeCode == '0102' || item.exe.typeCode == '0104'){ //多选题
          //判断是否选择了，如果已经选择，就取消选择
          if(i.selected == false){
            i.selected = true;
            $("#"+item.exerciseId+'_'+String.fromCharCode(65 + item.exe.exeOptionList.indexOf(i))).removeClass(String.fromCharCode(65 + item.exe.exeOptionList.indexOf(i)));
            $("#"+item.exerciseId+'_'+String.fromCharCode(65 + item.exe.exeOptionList.indexOf(i))).addClass(String.fromCharCode(65 + item.exe.exeOptionList.indexOf(i))+'-get');
            $("#"+String.fromCharCode(65 + item.exe.exeOptionList.indexOf(i))+'_'+item.exerciseId).css("color","#04CD9D");
          }else{
            i.selected = false;
            $("#"+item.exerciseId+'_'+String.fromCharCode(65 + item.exe.exeOptionList.indexOf(i))).removeClass(String.fromCharCode(65 + item.exe.exeOptionList.indexOf(i))+'-get');
            $("#"+item.exerciseId+'_'+String.fromCharCode(65 + item.exe.exeOptionList.indexOf(i))).addClass(String.fromCharCode(65 + item.exe.exeOptionList.indexOf(i)));
            $("#"+String.fromCharCode(65 + item.exe.exeOptionList.indexOf(i))+'_'+item.exerciseId).removeAttr("style");
          }
        }
        //判断当前题是否被选中
        var selectNum = false;
        for(var i = 0 ; i < this.allDataList.data.testpaperExerciseList[this.pageIndex-1].exe.exeOptionList.length ; i++){
          if(this.allDataList.data.testpaperExerciseList[this.pageIndex-1].exe.exeOptionList[i].selected ){
            selectNum = true;
            if(this.noSelectList.indexOf(this.pageIndex) != -1){
              this.noSelectList.splice(this.noSelectList.indexOf(this.pageIndex),1);//如果被选中了则删除
            }
          }
          if(this.allDataList.data.testpaperExerciseList[this.pageIndex-1].exe.exeOptionList[i].selected && this.selectList.indexOf(this.pageIndex) == -1){
            this.selectList.push(this.pageIndex);
            return
          }
          if(this.allDataList.data.testpaperExerciseList[this.pageIndex-1].exe.exeOptionList[i].selected == false && selectNum == false){
            if(this.selectList.indexOf(this.pageIndex) != -1){
              this.selectList.splice(this.selectList.indexOf(this.pageIndex),1);
            }
            if(this.noSelectList.indexOf(this.pageIndex) == -1){
              this.noSelectList.push(this.pageIndex);
            }
          }
        }
      },

     //初始化swiper
      showSwiper(num){
        const that = this;
        const mySwiper = new Swiper ('.swiper-container', {
          observer:true,//修改swiper自己或子元素时，自动初始化swiper
          observeParents:true,//修改swiper的父元素时，自动初始化swiper
          initialSlide: 1,
          disableOnInteraction: false,
          on:{
            sliderMove: function(){
              if(that.selectList.indexOf(that.pageIndex) == -1  && that.noSelectList.indexOf(that.pageIndex) == -1){
                that.noSelectList.push(that.pageIndex);
              }
              
            },
          },
        });
        //主要是这部分
        mySwiper.on('slideChangeTransitionStart', function () {
          if(that.isCanOver){
            that.getOver();
            return;
          }
          that.pageIndex = mySwiper.activeIndex + 1;//获取当前的下标
          $("#now-page").html(that.pageIndex);
          //判断该页面是否标记
          if(that.bjList.indexOf(that.pageIndex) == -1  && that.bjList.indexOf(that.pageIndex) == -1){
            $('.bottom-mes-mes-1').removeClass('bottom-mes-mes-1-select');
            $('.bottom-mes-mes-1').addClass('bottom-mes-mes-1-noselect');
          }else{
            $('.bottom-mes-mes-1').addClass('bottom-mes-mes-1-select');
            $('.bottom-mes-mes-1').removeClass('bottom-mes-mes-1-noselect');
          }
        });
        that.swipperMes = mySwiper;
        mySwiper.slideTo(num,0,false);
        that.pageIndex = num + 1;
        $('.bottom-mes-mes-1').removeClass('bottom-mes-mes-1-select');
        $('.bottom-mes-mes-1').addClass('bottom-mes-mes-1-noselect');
      },
       //倒计时
      getCode(){
        const that = this;
        setTimeout(function () {
          //获取当前时间
          var date = new Date();
          var now = date.getTime();
          //设置截止时间
          var endDate = that.endTime;
          var end = endDate.getTime();
          //时间差
          var leftTime = end-now;
          var fiveLeftTime = that.fiveTime - now;
          var tenLeftTime = that.tenTime - now;

          //定义变量 d,h,m,s保存倒计时的时间
          var d,h,m,s,fives,ten;
          if (leftTime>=0) {
            // d = Math.floor(leftTime/1000/60/60/24);
            // h = Math.floor(leftTime/1000/60/60%24);
            m = Math.floor(leftTime/1000/60);
            s = Math.floor(leftTime/1000%60);
            h = that.getNow(h);
            m = that.getNow(m);
            s = that.getNow(s);
          }
          if(fiveLeftTime > 0){
            fives = Math.floor(fiveLeftTime/1000%60);
          }
          if(tenLeftTime > 0){
            ten = Math.floor(tenLeftTime/1000%60);
          }
          if(m == undefined){
            m = "00";
          }
          if(s == undefined){
            s = "00";
          }
          that.timeMes = m + ':' + s;
            if(!that.isoverText){
            //五分钟倒计时
            if(that.timeMes <= '05:00' && that.timeMes > '04:55'){
              that.openFive();
              var fiveSeconds =  fives +  's';
              $('.fiveSeconds').html(fiveSeconds);
              if(!that.isShowFive){
                $('.lackofTime').css('display','block');
                $('.residueTime').css('display','block');
                that.isShowFive = true;
              }
            }else{
               if(!that.isShowFiveFale && that.timeMes <= '04:55' ){
                $('.lackofTime').css('display','none');
                $('.residueTime').css('display','none');
                that.isShowFiveFale  = true;
               }
            }
            //十秒倒计时自动交卷
            if(that.timeMes <= '00:10' && that.timeMes > '00:00'){
              that.openTen();
              $('.tenSeconds').html(ten + 's');
              if(!that.isShowTen){
                $('.lackofTime').css('display','block');
                //$('.residueTime').css('display','none');
                $('.timeWarning').css('display','block');
                that.isShowTen = true;
              }
            }else{
              if(!that.isShowTenFale && that.timeMes <= '00:00' ){
                that.isCanOver = true;
                $('.lackofTime').css('display','none');
                $('.timeWarning').css('display','none');
                that.isShowTenFale = true;
                $('.textMainBox').css('display','none');
              }
            }
            //时间结束自动交卷
            if(that.timeMes == '00:00'){
              // this.isShowJJbox = false;
              that.uploadDialogVisible = true;
              $('.textMainBox').css('display','none');
              $('.lackofTime').css('display','none');
              // this.isShowdtk = false;
              $('.textMainBox-dtk').css('display','none')
              that.getEndTime();
              return;
            }
          }
          if(!that.isOver){
            //递归每秒调用countTime方法，显示动态时间效果
            setTimeout(that.getCode(),1000);
          }
        })
      } ,
      //设置日期的格式
      getNow(s){
        return s < 10 && s >= 0 ? '0' + s: s;
      },
      //交卷模态框
      getOver(){
        this.noSelectNum = this.allDataList.data.testpaperExerciseList.length - this.selectList.length;
        const that = this;
        $('.textMainBox').css('display','flex');
        // this.isShowJJbox = true;
        this.isOpen = 1;
      },
      //现在交卷按钮
      nowPaper(){
        this.uploadDialogVisible = true;
        this.isoverText = true;
        // $('.textMainBox').css('display','none');
        $('.startText-mess-two-wait').css('display','none');
        $('.startText-mess-two-wait2').css('display','flex');
        $('.timeWarning').css('display','none');
        $('.lackofTime').css('display','none');
        this.isShowTen = false;
        this.isShowTenFale = false;
        this.isOpenFive = false;
        this.isShowFive = false;
        this.getEndTime();
      },
      //继续答题按钮
      continuePaper(){
        $('.textMainBox').css('display','none')
        // this.isShowJJbox = false;
      },
      //剩余 5分钟提示
      openFive() {
        if(this.isHandel == 0){
          this.isHandel = 1;
          this.fiveTime = new Date(new Date().getTime() + 1000*6);
        }
      },
      //交卷时获取结束时间并返回给接口
      getEndTime(){
        const that = this;
        this.endtime = new Date().getTime();
        this.spendTime =Math.floor((this.endtime - this.startTime)/1000/60);
        if(this.spendTime == 0){
          this.spendTime = 1;
        }
        this.allDataList.data.startTime = this.startTime;
        this.allDataList.data.endTime = this.endtime;
        this.allDataList.data.spendTime = this.spendTime;
        this.allDataList.data.identify = 1;
        this.allDataList.data.testpaperExerciseList = [];
        this.allDataList.data.testpaperExerciseList = this.receiveData;
        API.post('handInTestpaperAndJudge',
          that.allDataList.data
          , 'json',(data,err)=>{
            if(!data.success){
              this.uploadDialogVisible = false;
              this.IsBoxDialogVisible = true;
              this.mess = data.msg;
              return;
            }else if(data.success){
              that.isError = false;
              this.uploadDialogVisible = false;
              that.startCount = data.data.length;
              $('.successBox').css('display','flex');
              $('.textMainBox').css('display','none');
              // this.isShowJJbox = false;
              localStorage.setItem('gradePassOrNor',data.data.passOrNot);
              $('.bigBox1').css('display','none');
              $('.bigBox2').css('display','none');
              $('.bigBox3').css('display','none');
              localStorage.removeItem('resumeNum');
              setTimeout(function(){
                that.$router.replace({
                  name: 'text_grade',
                  params: {grade:data.data,backMes : 1}
                })
                $('.successBox').css('display','none');
                
              },1000);
              
            }
          })
          setTimeout(function(){
            if(that.isError == true){
              that.IsBoxDialogVisible = true;
              that.mess = '网络异常，请重新登录';
              $('.textMainBox').css('display','none');
            }
          },30000)
      },
     //剩余10分钟提示
      openTen() {
        if(this.isTenHandel == 0){
          this.isTenHandel = 1;
          this.tenTime = new Date(new Date().getTime() + 1000*11);
        }
      },
      //显示答题卡
      showDTK(){
        const that = this;
        $('.textMainBox-dtk').css('display','block');
        // this.isShowdtk = true;
        $('.dkt-message').css('display','block');
        $(".dkt-message-title").empty();
        $(".dkt-message-content").empty();
        // this.$alert( "",{dangerouslyUseHTMLString: true});
        var htmlMes = "";
        for(var i = 0 ; i < this.allDataList.data.testpaperExerciseList.length ; i++){
          htmlMes += "<div class='dtk-all'><div id='dtk-" + (i + 1) + "'class='add-list-all add-list-" + (i + 1) + "'>" + (i + 1) + "</div><div style='display:none;' id='add-div-"+(i + 1)+"'></div></div>"
        }
        
        var titleMes = "";
        var residue = this.receiveData.length - this.selectList.length;
        titleMes = 
          '<div class="dtk-mes-mes-0">答题卡</div>'+
          '<div class="bottom-mes-mes-dtk">'+
          
          '<div class="bottom-mes-mes-dtk-1 ">'+
          '<div class="bottom-mes-mes-1 bottom-mes-mes-1-select"></div>'+
          '<div class="bottom-mes-mes-1-1">'+this.bjList.length+'</div>'+
          '</div>'+
          '<div class="bottom-mes-mes-dtk-2">'+
          '<div class="bottom-mes-mes-2"></div>'+
          '<div class="bottom-mes-mes-2-2">'+this.selectList.length+'</div>'+
          '</div>'+
          '<div class="bottom-mes-mes-dtk-3">'+
          '<div class="bottom-mes-mes-3"></div>'+
          '<div class="bottom-mes-mes-3-3">'+ residue +'</div>'+
          '</div>'+
          '<div class="bottom-mes-mes-dtk-4" id = "closeDtk">'+
          '<div class="bottom-mes-mes-4" ></div>'+
          '<div class="bottom-mes-mes-5">'+'<span style="color:#333;" id="now-page"></span>'+'<span style="color:#999999;">/</span>'+'<span style="color: #666;">'+this.allDataList.data.testpaperExerciseList.length+'</span></span>'+'</div>'+
          '</div>'+
          '</div>'
        $(".dkt-message-title").append(titleMes);
        $(".dkt-message-content").append(htmlMes);
        $('#now-page').html(this.pageIndex);
        $('.el-button').html('');
        //定义关闭事件
        
        $('#closeDtk').on("click", function () {
          // that.isShowdtk = false;
          $('.textMainBox-dtk').css('display','none');
          $(".dkt-message-title").empty();
          $(".dkt-message-content").empty();
          $('.dkt-message').css('display','none');
        });
        //添加标记样式
        for(var i = 0 ; i < this.bjList.length ; i++){
          $('#add-div-' + this.bjList[i]).addClass('dtk-bj');
          $('#add-div-' + this.bjList[i]).css("display","")
        }
        var isAdd = true;
        //添加已选样式
        for(var i = 0 ; i < this.selectList.length ; i++){
          $('.add-list-' + this.selectList[i]).addClass('dtk-select');
          if(this.pageIndex == this.selectList[i]){
            isAdd = false;
          }
        }
        //添加当前题号样式
        if(isAdd){
          $('.add-list-' + this.pageIndex).addClass('dtk-pageIndex');
          $('.add-list-' + this.pageIndex).css('border','0');
        }
        
        // //添加未选样式
        // for(var i = 0 ; i < this.noSelectList.length ; i++){
        //   $('.add-list-' + this.noSelectList[i]).addClass('dtk-noselect');
        // }
        //动态给div添加点击事件
        for(var k = 0 ; k < this.allDataList.data.testpaperExerciseList.length ; k++){
          $('#dtk-' + (k+1)).on("click", function () {
            that.showSwiper(Number(this.innerText) - 1);
            for(var i = 0 ; i < that.bjList.length ; i++ ){
              if((Number(this.innerText)) == that.bjList[i] && (that.bjList.indexOf((Number(this.innerText))) != -1)){
                $('.bottom-mes-mes-1').removeClass('bottom-mes-mes-1-noselect');
                $('.bottom-mes-mes-1').addClass('bottom-mes-mes-1-select');
                continue;
              }
            }
            // $('.dkt-message').hide();
            // this.isShowdtk = false;
            $('.textMainBox-dtk').css('display','none');
          });
        }
      },
      //点击标记
      bjAdd(){
        var num = this.bjList.length;//原有数组的长度
        //循环数组，如果存在则在数组中去除，
        for(var i = 0 ; i < this.bjList.length ; i++ ){
          if(this.pageIndex == this.bjList[i] && this.bjList.indexOf(this.pageIndex) != -1){
            this.bjList.splice(this.bjList.indexOf(this.pageIndex),1);
            $('.bottom-mes-mes-1').addClass('bottom-mes-mes-1-noselect');
            $('.bottom-mes-mes-1').removeClass('bottom-mes-mes-1-select');
            continue;
          }
        }
        if(num == this.bjList.length){
          this.bjList.push(this.pageIndex);
          $('.bottom-mes-mes-1').addClass('bottom-mes-mes-1-select');
          $('.bottom-mes-mes-1').removeClass('bottom-mes-mes-1-noselect');
        }
      },
      //关闭答题卡的背景图
      closeDtkBox(){
        // this.isShowdtk = false;
        $('.textMainBox-dtk').css('display','none');
        $('.dkt-message').css('display','none');
      },
      IsBoxcloseVerDialogMethod(){
        this.IsBoxDialogVisible = false;
        this.$router.push({
            name:'text_list_wait',
            params:{fromText : 1 }
          });
        // this.$router.back();
      },
      //返回键
      onBackKeyDown(){
        if($('.text-main .remindtextMainBox').css('display') == 'block' || 
            this.uploadDialogVisible == true || 
            this.verDialogVisible == true ||
            this.IsBoxDialogVisible == true ||
            this.networkDialogVisible == true ||
            $('.text-main .bigBox1').css('display') == 'flex' ||
            $('.text-main .bigBox2').css('display') == 'flex' ||
            $('.text-main .bigBox3').css('display') == 'flex'
            ){
          return
        }else{
          if($('.text-main .successBox').css('display') == 'none'  &&
              this.uploadDialogVisible == false &&
              this.verDialogVisible == false &&
              this.IsBoxDialogVisible == false &&
              this.networkDialogVisible == false &&
              $('.text-main .bigBox1').css('display') == 'none' &&
              $('.text-main .bigBox2').css('display') == 'none' &&
              $('.text-main .bigBox3').css('display') == 'none'
              ){
            // alert('去到text_main界面')
            if($(".text-main .textMainBox-dtk").css("display") == 'block'){
              // alert('答题卡出现时')
              $('.text-main .textMainBox-dtk').css('display','none')
              return
            }else if($(".text-main .textMainBox-dtk").css("display") == 'none'){
              // alert('答题卡没出现')
              if($('.text-main .textMainBox').css('display')=='none'){
                // alert('当交卷窗口没出现时')
                $('.text-main .textMainBox').css('display','flex')
                return
              }else if($('.text-main .textMainBox').css('display')=='flex'){
                // alert('当交卷窗口出现时')
                $('.text-main .textMainBox').css('display','none')
                return
              }
              return
            }
          }else{
            return
          }
        }
      },
      //网络监听
      updateOnlineStatus(e) {
        const { type } = e;
        this.onLine = type === 'online';
        if(this.verDialogVisible == true){
          if(this.onLine == true){
            this.networkDialogVisible = false;
            this.getDataList();
            this.showSwiper(0);
            if(this.duration < 10){
              this.duration = '0' + this.duration
            }
            this.timeMes = this.duration + ':00' ;
            // console.log(this.timeMes)
            $('.dkt-message').css('display','none');
            if($('.text-main .remindtextMainBox').css('display') == 'none'){
              // console.log(2)
              this.startTime = new Date().getTime();
              var nowTime= new Date(new Date().getTime() + 1000*60* this.duration + 2000);
              this.endTime = nowTime;
              this.getCode();
            }else{
              // console.log(1)
              return
            }
          }else if(this.onLine == false){
            this.networkDialogVisible = true
          }
        }else{
          if(this.uploadDialogVisible == true){
            if(this.onLine == true){
              this.networkDialogVisible = false;
              // this.getDataList();
              this.getEndTime()
            }else if(this.onLine == false){
              this.networkDialogVisible = true;
            }
          }else{
            if(this.onLine == true){
              this.networkDialogVisible = false;
            }else if(this.onLine == false){
              this.networkDialogVisible = true
            }
          }
          
        }
      },
    },
    
     
    beforeDestroy(){
      window.removeEventListener('online',  this.updateOnlineStatus);
      window.removeEventListener('offline', this.updateOnlineStatus);
      document.removeEventListener('backbutton',this.onBackKeyDown,false);
    },
    created(){
      localStorage.removeItem('resumeNum');
      this.id = this.$route.params.id;
      this.duration = this.$route.params.duration;
      this.selfExamId = this.$route.params.selfExamId;
      this.getDataList();
    },
    mounted(){
       window.addEventListener('online',  this.updateOnlineStatus);
        window.addEventListener('offline', this.updateOnlineStatus);
        //监听返回键
      document.addEventListener('backbutton',this.onBackKeyDown,false);
        this.showSwiper(0);
        if(this.duration < 10){
          this.duration = '0' + this.duration
        }
        this.timeMes = this.duration + ':00' ;
        // console.log(this.timeMes)
        $('.dkt-message').css('display','none');
        if($('.text-main .remindtextMainBox').css('display') == 'none'){
          // console.log(2)
          this.startTime = new Date().getTime();
          var nowTime= new Date(new Date().getTime() + 1000*60* this.duration + 2000);
          this.endTime = nowTime;
          this.getCode();
        }else{
          // console.log(1)
          return
        }
        // var time= new Date();
        // var nowTime = time.getFullYear() +'-'+this.getNow(time.getMonth()+1)+"-"+this.getNow(time.getDate())+" "+this.getNow(time.getHours())+':'+this.getNow(time.getMinutes())+":"+this.getNow(time.getSeconds());
        // nowTime= new Date(new Date().getTime() + 1000*60* this.duration + 1000);
        // this.endTime = nowTime;
        // this.getCode(); 
  
       
    },
    watch:{
      timeMes(newval , oldval){
          if(newval == "undefined:undefined"){
              this.newval = "10:00";
          }
      },
      // isShowJJbox(val ,oldval){
      //   // console.log(val)
      //   if(val == true){
      //     this.isShowdtk = false;
